@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  /* Ini sudah diperbaiki (tidak ada spasi aneh di awal) */
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  /* Ini sudah diperbaiki (tidak ada spasi aneh di awal) */
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* ======================================================== */
/* ✅ 1. Style Peringatan (Layar Hitam Overlay) */
/* ======================================================== */
.rotate-device-warning {
  /* Sembunyi di laptop secara default */
  display: none; 
  position: fixed;
  inset: 0; /* (top: 0, left: 0, bottom: 0, right: 0) */
  background-color: #000000; /* Layar hitam total */
  color: white;
  z-index: 9999;
  
  /* Konten di tengah */
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: sans-serif;
}

/* ======================================================== */
/* ✅ 2. ATURAN BARU (Hanya untuk HP/Tablet Potret) */
/* ======================================================== */
@media (max-width: 1024px) and (orientation: portrait) {
  
  /* Sembunyikan aplikasi React kamu */
  #root {
    display: none;
  }
  
  /* Tampilkan peringatan layar hitam */
  .rotate-device-warning {
    display: flex !important; /* !important untuk memaksa tampil */
  }
}